<template>
  <div class="h-screen flex justify-center items-center">
    <div class="flex flex-col justify-center items-center">
      <h1 class="font-bold text-2xl mb-6">Пойдешь со мной на свидание?</h1>
      <div class="flex justify-center items-center">
        <button ref="yesButton"
          class="mr-5 font-bold text-xl border-2 border-black w-16 h-10 rounded-[15px] text-red-600 absolute top-[26vw] left-[45vw]">Да</button>
        <button ref="noButton"
          class="mr-5 font-bold text-xl border-2 border-black w-16 h-10 rounded-[15px] text-red-600 absolute top-[26vw] right-[45vw]">Нет</button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';

export default {
  setup() {
    let x = ref(0);
    let y = ref(0);
    const yesButton = ref(null);
    const noButton = ref(null);
    const buttonX = ref(0);
    const buttonY = ref(0);

    onMounted(() => {
      buttonX.value = noButton.value.offsetLeft;
      buttonY.value = noButton.value.offsetTop;
    });

    document.addEventListener('mousemove', (e) => {
      if (
        e.pageX >= buttonX.value &&
        e.pageX <= buttonX.value + noButton.value.offsetWidth &&
        e.pageY >= buttonY.value &&
        e.pageY <= buttonY.value + noButton.value.offsetHeight
      ) {
        console.log(window.innerHeight, window.innerWidth)
        noButton.value.style.position = 'fixed';
        noButton.value.style.top = Math.floor(Math.random() * window.innerHeight-50) + 'px';
        noButton.value.style.left = Math.floor(Math.random() * window.innerWidth-50) + 'px';
        buttonX.value = noButton.value.offsetLeft;
        buttonY.value = noButton.value.offsetTop;
      }
    });

    return { x, y, yesButton, noButton, buttonX, buttonY };
  }
}
</script>

